import React, { useState, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import Alert from '@mui/material/Alert';
import CircularProgress from '@mui/material/CircularProgress';
import Editor from '@monaco-editor/react';
import yaml from 'js-yaml';
import { uploadYaml } from '../services/api';

const defaultYaml = `---
presentation:
  metadata:
    title: "My Presentation"
    author: "Your Name"
    date: "2025-03-07"
    default_font:
      family: "Arial"
      size: 12
      color: "#000000"

  pages:
    - page:
        title: "Introduction"
        layout: "title_and_content"
        background:
          color: "#FFFFFF"
        content_blocks:
          - block:
              type: "text"
              position:
                x: "10%"
                y: "25%"
                width: "80%"
                height: "50%"
              content:
                text: "Welcome to my presentation!"
                font:
                  size: 18
                alignment: "center"
`;

const HomePage: React.FC = () => {
  const [yamlContent, setYamlContent] = useState<string>(defaultYaml);
  const [isValid, setIsValid] = useState<boolean>(true);
  const [error, setError] = useState<string>('');
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [success, setSuccess] = useState<string>('');
  const fileInputRef = useRef<HTMLInputElement>(null);
  const navigate = useNavigate();

  const handleEditorChange = (value: string | undefined) => {
    if (!value) return;
    setYamlContent(value);
    validateYaml(value);
  };

  const validateYaml = (content: string) => {
    try {
      yaml.load(content);
      setIsValid(true);
      setError('');
    } catch (err) {
      setIsValid(false);
      setError(`Invalid YAML: ${(err as Error).message}`);
    }
  };

  const handleFileUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (e) => {
      const content = e.target?.result as string;
      setYamlContent(content);
      validateYaml(content);
    };
    reader.readAsText(file);
  };

  const handleDragOver = (event: React.DragEvent<HTMLDivElement>) => {
    event.preventDefault();
    event.currentTarget.classList.add('active');
  };

  const handleDragLeave = (event: React.DragEvent<HTMLDivElement>) => {
    event.currentTarget.classList.remove('active');
  };

  const handleDrop = (event: React.DragEvent<HTMLDivElement>) => {
    event.preventDefault();
    event.currentTarget.classList.remove('active');
    
    const file = event.dataTransfer.files?.[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (e) => {
      const content = e.target?.result as string;
      setYamlContent(content);
      validateYaml(content);
    };
    reader.readAsText(file);
  };

  const handleGenerate = async () => {
    if (!isValid) return;
    
    setIsLoading(true);
    setError('');
    setSuccess('');
    
    try {
      const response = await uploadYaml(yamlContent);
      setSuccess(`Presentation generated successfully! ID: ${response.id}`);
      setTimeout(() => {
        navigate('/presentations');
      }, 2000);
    } catch (err) {
      setError(`Failed to generate presentation: ${(err as Error).message}`);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <Box>
      <Typography variant="h4" component="h1" gutterBottom>
        PowerPoint Generator
      </Typography>
      <Typography variant="body1" paragraph>
        Create beautiful PowerPoint presentations from YAML definitions. Edit the YAML below or upload a file.
      </Typography>

      <Paper elevation={3} sx={{ p: 3, mb: 3 }}>
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <div
              className="file-upload-area"
              onDragOver={handleDragOver}
              onDragLeave={handleDragLeave}
              onDrop={handleDrop}
              onClick={() => fileInputRef.current?.click()}
            >
              <Typography variant="body1">
                Drag and drop a YAML file here, or click to select a file
              </Typography>
              <input
                type="file"
                ref={fileInputRef}
                onChange={handleFileUpload}
                accept=".yaml,.yml"
                style={{ display: 'none' }}
              />
            </div>
          </Grid>

          <Grid item xs={12}>
            <Typography variant="h6" gutterBottom>
              YAML Editor
            </Typography>
            <div className="yaml-editor">
              <Editor
                height="400px"
                defaultLanguage="yaml"
                value={yamlContent}
                onChange={handleEditorChange}
                options={{
                  minimap: { enabled: false },
                  scrollBeyondLastLine: false,
                  wordWrap: 'on',
                }}
              />
            </div>
            {error && (
              <Alert severity="error" sx={{ mt: 2 }}>
                {error}
              </Alert>
            )}
            {success && (
              <Alert severity="success" sx={{ mt: 2 }}>
                {success}
              </Alert>
            )}
          </Grid>

          <Grid item xs={12} className="generation-options">
            <Button
              variant="contained"
              color="primary"
              size="large"
              onClick={handleGenerate}
              disabled={!isValid || isLoading}
              startIcon={isLoading ? <CircularProgress size={20} color="inherit" /> : null}
            >
              {isLoading ? 'Generating...' : 'Generate PowerPoint'}
            </Button>
          </Grid>
        </Grid>
      </Paper>
    </Box>
  );
};

export default HomePage;